		return {
			selm:[false,false,false,false,false],
			user:"760495276@163.com",
			itemList: ['123456789@163.com', '987654654@163.com', '444586992@163.com']
		}
	},
	onLoad: function(e) {
		that = this;
	},
	onShow: function(res) {
		
	},
	methods: {
		selms:function(e){
			that.selmrefresh();
			that.selm[e]=true;
			console.log(e);
		},
		selmrefresh:function(){
			that.selm=[false,false,false,false,false];
		},
		seluser:function(e){
