			    success: function (res) {
					that.user=that.itemList[res.tapIndex];
			        console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
			    },
			    fail: function (res) {
			        console.log(res.errMsg);
			    }
			});
		}
	}

}
